import { Link } from 'react-router-dom';

function Navbar() {
  return (
    <nav className="bg-primary text-white shadow-md">
      <div className="container mx-auto flex items-center justify-between h-16 px-6 lg:px-8">
        {/* Logo */}
        <Link to="/" className="text-3xl font-bold leading-none">
          
        </Link>

        {/*  */}
        <div className="flex items-center space-x-8">
          {[
            { to: '/', label: '' },
            { to: '/health-info', label: '' },
            { to: '/health-log', label: '' },
            { to: '/healthy-recipes', label: '' },
            { to: '/hospital-map', label: '' },
          ].map((item) => (
            <Link
              key={item.to}
              to={item.to}
              className="relative text-lg font-medium leading-none py-1 hover:text-white/80"
            >
              {item.label}
              {/* hover  */}
              <span className="absolute left-0 right-0 bottom-0 h-0.5 bg-transparent hover:bg-white transition-all"></span>
            </Link>
          ))}
        </div>
      </div>
    </nav>
  );
}

export default Navbar;
